import Engine from './engine/Engine';
import AR from './ar/AR';
import Marker from './ar/Marker';
import Render from './render/Render';
import { IOS_PLATFORM, ANDROID_PLATFORM } from './tools/Env';
import NATIVE_RROGRESS_CLOSE from './tools/Native';
import { WEB_AR_ERROR, WEB_AR_ENGINE_STATUS_CHANGE } from './tools/Watch';

const ENV = {
    IOS: IOS_PLATFORM,
    ANDROID: ANDROID_PLATFORM
};
const QBUtils = {
    CLOSE_AR_PLAYER: NATIVE_RROGRESS_CLOSE // 关闭QQ浏览器AR播放器 native加载条
};
const TARUtils = {
    // tar辅助功能 1.WEB_AR_ERROR 获取android ar初始化失败信息 2.WEB_AR_ENGINE_STATUS_CHANGE 获取ar引擎加载情况
    ERROR: WEB_AR_ERROR,
    STATUS_CHANGE: WEB_AR_ENGINE_STATUS_CHANGE
};
export {
    Engine, AR, Marker, Render, ENV, QBUtils, TARUtils
};


